<template>
	<view class="auth">
		<!-- 导航栏 开始 -->
		<uni-nav-bar fixed background-color="#d81e06" color="#fff" :status-bar="true" @clickLeft="leftNavTap">
		    <view>授权</view>
		    <view slot="left"> 
				<text class="iconfont icon-houtui"></text> 
			</view>
			<view slot="right"></view>
		</uni-nav-bar>
		<!-- 导航栏 结束 -->
		<button @getphonenumber="getPhoneNumber" open-type="getPhoneNumber">一键授权</button>
	</view>
</template>

<script>
	export default {
		data(){
			return{
				
			}
		},
		methods:{
			//返回上一级页面
			leftNavTap(){
				uni.navigateBack();
			},
			//获取用户手机号信息
			getPhoneNumber(e){
				if(e.detail.errMsg === "getPhoneNumber:ok"){
					setTimeout(_=>{
						uni.navigateBack();
						uni.setStorageSync('token','123');
					}, 200);
				} else{
					uni.showToast({
					    title: '请您点击进行授权',
					    duration: 2000,
						icon:'none'
					});
				}
			},
		},
		onLoad(option){
			console.log(option);
		}
	}
</script>

